import React from 'react';
import Style from './Dialog.module.scss'
import DialogItem from './DialogItem/DialogItem';
import Message from './Message/Message';
import { Form, Field } from 'react-final-form'
import { required } from '../../utils/validators/validators';
import { Textarea } from '../common/FormsControls/FormsControls';

function MessageForm(props) {
    return (
        <Form onSubmit={props.submit} render={({ handleSubmit, submitting, pristine }) => (
            <form onSubmit={handleSubmit} className={Style.formArea}>
                <Field name='message' component={Textarea} rows={4} validate={required} />
                <button disabled={submitting || pristine} >Add Message</button>
            </form>
        )}
        />
    );
}

export default function Dialogs(props) {
    let dialogData = props.state.dialogs.map((item) => (<DialogItem name={item.name} id={item.id} key={item.id} />));
    let messagesElements = props.state.messages.map((item) => <Message message={item.message} key={item.id} />);

    const handleClick = (values) => {
        props.messageAdd(values.message);
    }
    return (
        <div className={Style.colorDiv}>
            <div className={Style.dialogColumn}>
                <h2>Dialogs</h2>
                <ul className={Style.listName}>
                    {dialogData}
                </ul>
            </div>
            <div className={Style.messageColumn}>
                <h2>Messages</h2>
                <ul className={Style.listMessages}>
                    {messagesElements}
                </ul>
                <MessageForm submit={handleClick} />
            </div>
        </div>
    );
}