import { userAPI } from '../api/api';

const FOLLOW = 'FOLLOW';
const SET_USERS = 'SET-USERS';
const SET_CURRENT_PAGE = 'SET-CURRENT-PAGE';
const SET_TOTAL_USERS_COUNT = 'SET-TOTAL-USERS-COUNT';
const TOGGLE_IS_FETCHING = 'TOGGLE-IS-FETCHING';
const TOGGLE_IS_FOLLOWING = 'TOGGLE-IS-FOLLOWING';

let initialState = {
    users: [],
    pageSize: 8,
    totalCount: 37,
    currentPage: 1,
    isFetching: false,
    followingInProgress: []
}

export default function UsersReducer(state = initialState, action) {
    switch (action.type) {
        case FOLLOW:
            return {
                ...state,
                users: state.users.map(item => {
                    if (item.id == action.userId) {
                        return { ...item, followed: !item.followed }
                    }
                    return item;
                })
            }
        case SET_USERS:
            return { ...state, users: [...action.users] }
        case SET_CURRENT_PAGE:
            return { ...state, currentPage: action.currentPage }
        case SET_TOTAL_USERS_COUNT:
            return { ...state, totalCount: action.currentUserCount }
        case TOGGLE_IS_FETCHING:
            return { ...state, isFetching: action.isFetching }
        case TOGGLE_IS_FOLLOWING:
            return {
                ...state,
                followingInProgress: action.isFollowing ?
                    [...state.followingInProgress, action.userId] :
                    state.followingInProgress.filter(id => id != action.userId)
            }
        default: return state;
    }
}
export const followAC = (userId) => ({ type: FOLLOW, userId });
export const setUsersAC = (users) => ({ type: SET_USERS, users });
export const setCurrentPageAC = (currentPage) => ({ type: SET_CURRENT_PAGE, currentPage });
export const setTotalUserCountAC = (currentUserCount) => ({ type: SET_TOTAL_USERS_COUNT, currentUserCount });
export const setIsFetchingAC = (isFetching) => ({ type: TOGGLE_IS_FETCHING, isFetching });
export const setFollowingInProgressAC = (isFollowing, userId) => ({ type: TOGGLE_IS_FOLLOWING, isFollowing, userId });

const getCurrentUsers=async (dispatch, needPage, pageSize,needTotalCount )=>{
    dispatch(setIsFetchingAC(true));
    const data = await userAPI.getUsers(needPage, pageSize);
    dispatch(setIsFetchingAC(false));
    dispatch(setUsersAC(data.items));
    if(needTotalCount){
        dispatch(setTotalUserCountAC(data.totalCount));
    }
}

export const getUsersThunkCreator = (currentPage, pageSize) => {
    return async (dispatch) => {
        getCurrentUsers(dispatch, currentPage, pageSize, true)
    }
}

export const getNewPageUsersTC = (pageNumber, pageSize) => {
    return async (dispatch) => {
        dispatch(setCurrentPageAC(pageNumber));
        getCurrentUsers(dispatch,pageNumber, pageSize, false )
    }
}

const followFlow=async(dispatch, apiMethod, id)=>{
    const response = await apiMethod(id);
    if (response.data.resultCode == 0) {
        dispatch(followAC(id));
        dispatch(setFollowingInProgressAC(false, id));
    }
}

export const changeFollowTC = (id) => {
    return async (dispatch) => {
        const apiMethod=userAPI.follow.bind(userAPI);
        followFlow(dispatch, apiMethod, id)
    }
}
export const changeUnfollowTC = (id) => {
    return async (dispatch) => {
        const apiMethod=userAPI.unfollow.bind(userAPI);
        followFlow(dispatch, apiMethod, id)
    }
}