const ADD_MESSAGE = 'ADD-MESSAGE';

let initialState = {
    dialogs: [
        { id: 1, name: 'Sveta' },
        { id: 2, name: 'Misha' },
        { id: 3, name: 'Sveta' },
        { id: 4, name: 'Viktoria' },
        { id: 5, name: 'Dima' },
        { id: 6, name: 'Mariana' }
    ],
    messages: [
        { id: 1, message: 'Hello, I like JacaScript' },
        { id: 2, message: 'Hello! Nice to meet you. I like it too' },
        { id: 3, message: 'Do you like use VS?' },
        { id: 4, message: 'I dont know' }
    ]
};
export default function dialogReducer(state = initialState, action = { type: '' }) {
    switch (action.type) {
        case ADD_MESSAGE: {
            return {
                ...state,
                newMessageText: '',
                messages: [...state.messages, { id: 6, message: action.message }]
            }
        }
        default: return state;
    }
}

export const addMessageActionCreator = (message) => {
    return { type: ADD_MESSAGE, message };
};
