import React, { useEffect, useState } from "react";

export default function ProfileStatusWithHooks(props) {
    const [editMode, setEditMode] = useState(false);
    const [status, setStatus] = useState(props.status);

    useEffect(()=>{
        setStatus(props.status);
    }, [props.status]);

    const handleActivateDoubleClick = () => {
        setEditMode(true);
    }
    const handleDeactivateDoubleClick = () => {
        setEditMode(false);
        props.updateStatus(status);
    }
    const handleChange=(e)=>{
        setStatus(e.currentTarget.value)
    }
    return (
        <div>
            {!editMode ?
                <span onDoubleClick={handleActivateDoubleClick} >{props.status || '----'}</span> :
                <input autoFocus={true} onBlur={handleDeactivateDoubleClick} onChange={handleChange} value={status} />
            }
        </div>
    );
}