import { profileAPI } from "../api/api";
const ADD_POST = 'ADD-POST';
const SET_USER_PROFILE = 'SET-USER-PROFILE';
const SET_STATUS = 'SET-STATUS';
const DELETE_POST='DELETE-POST';

let initialState = {
    posts: [
        { id: 1, message: 'Hi, How are you?', likesCount: 12 },
        { id: 2, message: 'It is my new post', likesCount: 21 },
        { id: 3, message: 'VS is cool', likesCount: 1 },
        { id: 4, message: 'Are you sure', likesCount: 5 },
        { id: 5, message: 'VS is not cool. I like WebStorm', likesCount: 1 }
    ],
    profile: null,
    isFetching: false,
    status: ''
}
export default function profileReducer(state = initialState, action = { type: '' }) {
    switch (action.type) {
        case ADD_POST: {
            return {...state, 
                posts: [...state.posts, {id: 5, message: action.message, likesCount: 0}] };
        }
        case SET_USER_PROFILE:
            return { ...state, profile: action.profile }
        case SET_STATUS:
            return { ...state, status: action.status }
        case DELETE_POST:
            return {...state, posts:state.posts.filter(item=> item.id!=action.userId)}
        default: return state;
    }
}
export const addPostActionCreator = (message) => {
    return { type: ADD_POST, message };
};
export const setUserProfileAC = (profile) => {
    return { type: SET_USER_PROFILE, profile }
}
export const setStatusAC = (status) => {
    return { type: SET_STATUS, status }
}
export const deletePostAC = (userId) => {
    return { type: DELETE_POST, userId }
}
export const getCurrentProfileTC = (userId) => {
    return (dispatch) => {
        profileAPI.getCurrentProfile(userId).then(response => {
            dispatch(setUserProfileAC(response.data));
        })
    }
}
export const getUserStatusTC = (userId) => {
    return (dispatch) => {
        profileAPI.getStatus(userId).then(response => {
            dispatch(setStatusAC(response.data));
        })
    }
}
export const updateUserStatusTC = (status) => {
    return (dispatch) => {
        profileAPI.updateStatus(status).then(response => {
            if (response.data.resultCode == 0) {
                dispatch(setStatusAC(status));
            }
        })
    }
}
