import React from "react";
import { connect } from "react-redux";
import Profile from "./Profile";
import { setUserProfileAC, getCurrentProfileTC, getUserStatusTC, updateUserStatusTC } from '../../redux/profile-reducer'
import { useParams } from "react-router-dom";
import withAuthRedirect from '../../hoc/withAuthRedirect';
import { compose } from "redux";


const withRouter = WrappedComponent => props => {
  let params = useParams();
  // etc... other react-router-dom v6 hooks
  return (
    <WrappedComponent
      {...props}
      params={params}
    // etc...
    />
  );
};

class ProfileContainer extends React.Component {
  componentDidMount() {
    let userId = this.props.params.userId;
    if (!userId) {
      if(this.props.isAuth){
        userId = this.props.logId;
      }
    }
    this.props.getCurrentProfileTC(userId);

    this.props.getUserStatusTC(userId);
 
    /* profileAPI.getCurrentProfile(userId).then(response => {
       this.props.setUserProfile(response.data);
     })*/
  }
  render() {

    return (
      <>
        <Profile {...this.props} profile={this.props.profile} status={this.props.status} updateStatus={this.props.updateUserStatusTC}/>
      </>
    );
  }
}


let mapStateToProps = (state) => ({
  profile: state.profilePage.profile,
  status: state.profilePage.status,
  logId:state.auth.id,
  isAuth:state.auth.isAuth
});

export default compose(
  connect(mapStateToProps, {
    setUserProfile: setUserProfileAC,
    getCurrentProfileTC,
    getUserStatusTC,
    updateUserStatusTC
  }),
  withRouter
 // withAuthRedirect
)(ProfileContainer);
