import React from "react";
import Style from './Login.module.scss';
import { Form, Field } from 'react-final-form'
import { Navigate } from "react-router-dom";
import { required } from "../../utils/validators/validators";
import { Input } from "../common/FormsControls/FormsControls";

const LoginForm = (props) => {
    return (
        <Form onSubmit={props.onSubmit} render={({ handleSubmit }) => (
            <form onSubmit={handleSubmit} className={Style.formStyle}>
                <Field name="login" component={Input} type="text" placeholder="login" validate={required} defaultValue='dsds' />
                <Field name="password" component={Input} type="password" placeholder="password" validate={required} />
                <label>
                    <Field type="checkbox" component="input" name="rememberMe" />
                    Remember me
                </label>
                <button>Login</button>
            </form>
        )} />
    );
}

export default function Login(props) {
    const onSubmit = values => {
        props.LoginTC(values.login, values.password, true);
    }
    return (
        <div>
            {props.isAuth ?
                <Navigate to={"/profile"} /> :
                <div className={Style.container}>
                    <h1>Login</h1>
                    {props.errorLoginform ? <span>{props.errorLoginform}</span> : null}
                    <LoginForm onSubmit={onSubmit} />
                </div>
            }
        </div>
    );
}